.portfolio{
    overflow: hidden;
    position: relative;
    height: 365px;
}
.slick-arrow.slick-next{
    position:absolute;
    top: 50%;
    right: 0;
    z-index: 1;
    top: 50%;
    font-size:0;
    background:url(../img/slider/next.png) center center/cover no-repeat;
    height: 45px;
    width: 25px;
    border: none;
}
.slick-arrow.slick-prev{
    position:absolute;
    top: 50%;
    left: 0;
    z-index: 1;
    font-size:0;
    background:url(../img/slider/last.png) center center/cover no-repeat;
    height: 45px;
    width: 25px;
    border: none;
}