.contact{
  min-height: 156px;
  background-color: #25262a;
  padding: 83px 0 83px 0;
  color: #ffffff;
  font-family:'Roboto';
  font-size: 14px;
  font-weight: 400;
  &__wraper{
    width: 1078px;
    margin: 0 auto;
    display: flex;
  }
  &__item{
    margin-right: 137px;
    &:last-child{
      margin: 0;
      li{a{font-weight: 700;}}
    }
  }
  &__title{
    margin-bottom: 50px;
  }
  &__link{
    color: white;
  }
  &__li{
    margin-top: 21px;
    &:first-child{
      margin-top: 0;
    }
  }
  &__ul{
    margin: 0;
  }
}
.concatac__ul_p20{
  li{
    &:first-child{
      a{
        padding-left: 18px;
        position: relative;
        &::after{
          content: '';
          position: absolute;
          background: url(../img/contact/tel.png);
          height: 15px;
          width: 16px;
          left: -18px;
          top:calc(50%-15px);
        }
      }
    }
    &:last-child{
      a{
        padding-left: 18px;
        position: relative;
        &::after{
          content: '';
          position: absolute;
          background: url(../img/contact/mail.png);
          height: 11px;
          width: 16px;
          left: -18px;
          top:20%;
        }
      }
    }
    &:nth-of-type(2){
      a{
        padding-left: 18px;
        position: relative;
        &::after{
          content: '';
          position: absolute;
          background: url(../img/contact/air.png);
          height: 15px;
          width: 16px;
          left: -18px;
          top:11%;
        }
      }
    }
  }
}
@media (max-width:1140px) {
  .contact{
    text-align: center;
    &__wraper{
      width: 240px;
      flex-direction: column;
      align-items: flex-start;
    }
    &__item {
      width: 100%;
      margin-right: 0;
    }
  }
}