.menu{
    min-height: 590px;
    background-color: #f3f4f7;
    padding: 110px 0 109px 0;
    &__wraper{
        width: 1105px;
        margin: 0 auto;
        margin-top: 146px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: wrap;
    }
    &__item-container{
        display: flex;
        justify-content: space-between;
    }
    &__item{
        width: 458px;
        margin-top: 62px;
        &:nth-child(1),&:nth-child(2){
            margin-top: 0;
        }
    }
    &__title{
        color: #0b0c0d;
        font-size: 16px;
        font-weight: 400;
        text-transform: uppercase;
    }
    &__price{
        color: #c8235d;
        font-size: 16px;
        font-weight: 400;
    }
    &__descr{
        opacity: 0.7;
        color: #0b0c0d;
        font-size: 16px;
        font-weight: 400;
    }
}
@media (max-width:1140px){
    .menu{
        &__wraper{
            width: 372px;
        }
        &__item{
            width: 458px;
            margin-top: 62px;
            &:nth-child(2){
                margin-top: 62px;
            }
            &:nth-child(1){
                margin-top: 0;
            }
        }
    }
}
@media (max-width:576px){
    .menu{
        &__item{
            width: 310px;
        }
        &__wraper{
            width: 307px;
        }
    }
}