.header{
    color: #ffffff;
    height: 100vh;
    background: url(../img/header/header_bg.png) center center/cover no-repeat;
    &__wraper{
        min-width: 713px;
        margin: 0 auto;
        text-align: center;
    }
    &__title{
        margin-top: 327px;
        font-family: "NovaT";
        font-size: 72px;
        font-weight: 400;
        line-height: 32px;
        text-transform: uppercase;
    }
}
.nav{
    max-width: 1211px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 62px;
    &__ul{
        display: flex;
    }
    &__li{
        a{
            color: #ffffff;
            font-size: 16px;
            font-weight: 400;
        }
    }
    &__logo{
        a{
            color: #ffffff;
            font-family: "NovaT";
            font-size: 31px;
            font-weight: 400;
            line-height: 32px;
        }
    }
}
@media (max-width:1240px){
    .nav{
        max-width: 1011px;
    }
    .header{
        &__wraper{
            width: 313px;
        }
    }
}
@media (max-width:1140px){
    .nav{
        .nav__ul{
            display: none;
        }
        .brg{
            display: block;
        }
        .nav__wraper{
            z-index: 1000;
            transition: all 0.3s ease;
            display: flex;
            align-items: center;
            justify-content: center;
            position: fixed;
            height: 100vh;
            width: 30%;
            bottom: 0;
            left: -100%;
            background-color: rgba($color: #000000, $alpha: .95);
            .nav__ul{
                display: block;
                li{
                    margin-top: 40px;
                    &:first-child{
                        margin-top: 0;
                    }
                    a{
                        font-size: 40px;
                    }
                }
            }
        }
        .nav__wraper.active{
            transition: all 0.3s ease;
            left: 0;
        }
    }
}
@media (max-width:1024px){
    .nav{
        width: 800px;
    }
    .nav .nav__wraper .nav__ul li a{
        font-size: 30px;
    }
}
@media (max-width:820px){
    .nav{
        width: 700px;
    }
    .header__title{
        font-size: 62px;
    }
    .subtitle_header{
        font-size: 14px;
    }
    .header__wraper{
        min-width: 574px;
    }
}
@media (max-width:720px){
    .nav{
        width: 600px;
    }
    .header__title{
        font-size: 50px;
    }
    .subtitle_header{
        font-size: 12px;
    }
    .header__wraper{
        min-width: 440px;
    }
    .nav .nav__wraper .nav__ul li a{
        font-size: 25px;
    }
}
@media (max-width:620px){
    .nav{
        width: 500px;
    }
    .header__title{
        font-size: 40px;
    }
    .subtitle_header{
        font-size: 10px;
    }
    .header__wraper{
        min-width: 400px;
    }
    .nav .nav__wraper .nav__ul li a{
        font-size: 19px;
    }
}
@media (max-width:520px){
    .nav .nav__wraper {
        width: 45%;
    }
    .nav{
        width: 400px;
    }
    .nav .nav__wraper .nav__ul li a{
        font-size: 15px;
    }
}
@media (max-width:420px){
    .nav{
        width: 300px;
    }
    .header__title{
        font-size: 28px;
    }
    .header__wraper{
        min-width: 300px;
    }
}
