.story{
    min-height: 490px;
    background-color: #962323;
    padding: 76px 0 76px 0;
    display: flex;
    justify-content: center;
    &__wraper{
        width: 1116px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
    }
    &__text{
        width: 508px;
    }
}
@media (max-width:1140px){
    .story{
        &__wraper{
            display: flex;
            flex-direction: column;
        }
    }
}
@media (max-width:576px){
    .story{
        &__text{
            width: 308px;
        }
        &__img{
            width: 300px;
        }
        .subtitle_story{
            font-size: 12px;
        }
    }
}