.footer{
  min-height:122px;
  background-color: #16171b;
  &__logo{
	font-family: 'Roboto';
  }
  &__wraper{
	width: 1082px;
	padding-top: 46px;
    margin: 0 auto;
    display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
    color: #4b4c4e;
    font-size: 16px;
    line-height: 24px;
	letter-spacing: 1.6px;
    a{
      color: #4b4c4e;
      font-family: 'Roboto';
      font-size: 16px;
      line-height: 24px;
      letter-spacing: 1.6px;
    }
  }
  &__nav-ul{
	display: flex;
	li{
		margin-right: 24px;
		&:last-child{
			margin-right: 0;
		}
	}
  }
  &__icon-list{
	display: flex;
    li{
      margin-right: 21px;
      i{
        opacity: 0.5;
        color: #ded4fd;
      }
      i:hover{
        color: #fff;
        opacity: 1;
      }
    }
  }
}
@media (max-width:1140px){
	.footer{
		&__wraper{
			width: 408px;
			flex-direction: column;
		}
	}
}
@media (max-width:576px){
	.footer{
		&__wraper{
			font-size: 12px;
			width: 320px;
			a{
				font-size: 14px;
			}
		}
		&__nav-ul{
			display: flex;
			li{
				margin-right: 6.4px;
			}
		  }
	}
}