@import "../libs/normalize.scss";
@import "base/fonts.scss";
*{
    font-family: 'NovaA';
    font-weight: 400;
}
a{
    text-decoration: none;
}
ul{
    padding: 0;
}
.nav li{
    margin-right: 50px;
}
li{
    list-style: none;
    &:last-child{
        margin-right: 0;
    }
}
.title{
    color: #ffffff;
    font-family: "forevert";
    font-size: 56px;
    font-weight: 400;
    line-height: 32px;
    text-align: center;
    position: relative;
    &::before{
        content:'';
        position: absolute;
        width: 108px;
        height: 2px;
        background-color: #c8235d;
        bottom: -30px;
        right: 46%;
    }
    &_story{
        margin-bottom: 70px;
        &::before{
            background-color: white;
            right: 40%;
        }
    }
    &_menu{
        color: #0b0c0d;
        &::before{
            right: 47%;
        }
    }
}
.subtitle{
    font-family: "NovaA";
    font-size: 18px;
    font-weight: 400;
    line-height: 32px;
    text-align: center;
    color: white;
    &__header{
        margin-top: 52px;
    }
    &_recipes{
        width: 458px;
        margin: 0 auto;
        margin-top: 72px;
    }
}
.brg{
    display: none;
    height: 30px;
    width: 40px;
    span{
        transition: all 0.3s ease;
        display: block;
        width: 40px;
        height: 2px;
        background-color: white;
        position: relative;
        &::before,&::after{
            content: '';
            position: absolute;
            top: 14px;
            background-color: white;
            width: 40px;
            height: 2px;
            transition: all 0s;
        }
        &::after{
            top: 28px;
            transition: all 0.3s ease;
        }
    }
}
.brg.active span{
    transform: rotate(45deg);
    top: 50%;
    &::before{
        display: none;
    }
    &::after{
        transform: rotate(-90deg);
        top: -50%;
    }
}
@import "block/header.scss";
@import "block/story.scss";
@import "block/recipes.scss";
@import "block/menu.scss";
@import "block/Portfolio.scss";
@import "block/contact.scss";
@import "block/footer.scss";